import { useEditorDevice } from "@/hooks/use-editor-device";
import { usePreview } from "@/hooks/use-preview";
import { Container } from "@/components/editor-ui/container";
import clsx from "clsx";
import React from "react";

export const Editor = () => {
  const [isPreviewMode] = usePreview();
  const [device] = useEditorDevice();

  return (
    <div
      className={clsx("flex h-full flex-col items-center overflow-scroll", {
        "mr-[385px]": !isPreviewMode,
      })}
    >
      <div
        className={clsx("h-full w-full overflow-scroll", {
          "w-[850px]": device === "Tablet",
          "w-[420px]": device === "Smartphone",
          "border border-dashed border-slate-600 p-4": !isPreviewMode,
        })}
      >
        <Container
          isRoot
          slots={{
            content: [<div className="w-full bg-slate-600">CONTENT SLOT</div>],
          }}
        />
      </div>
    </div>
  );
};
